:root {
  color-scheme: light;
  --text-color: #242424;
  --background-color: #fff;
  --surface-color: #e7e7e7;
  --accent-color: #242424;
  --accent-text-color: #fff;
  --error-color: #fa8796;
}

:focus-visible {
  z-index: 1;
  box-shadow: 0 0 0 .125rem var(--text-color);
  outline: none;
  transition: box-shadow .1s ease-in;
}

::placeholder {
  color: var(--text-color);
  opacity: .5;
}

* {
  color: inherit;
  background-color: inherit;
}

html {
  font: 16px Poppins, sans-serif;
}

body {
  color: var(--text-color);
  background-color: var(--background-color);
  padding: 1rem;
  line-height: 1.5;
}

button {
  height: 2.25em;
  background-color: var(--surface-color);
  font: inherit;
  border: none;
  border-radius: .5rem;
  padding: 0 .75em;
}

dialog[open] {
  flex-direction: column;
  display: flex;
}

dialog {
  width: min(100%, 350px);
  text-align: center;
  -webkit-user-select: none;
  user-select: none;
  border: none;
  border-radius: .5rem;
  gap: .25rem;
  padding: 1rem;
  box-shadow: 0 0 100px #00000040;
}

dialog::backdrop {
  opacity: .25;
  background-color: #000;
}

dialog > div:last-of-type {
  flex-direction: row;
  justify-content: flex-end;
  gap: .25rem;
  padding-top: .5rem;
  display: flex;
}

dialog > div:last-of-type > button {
  flex: 1;
}

input, select {
  height: 2.25em;
  background-color: var(--surface-color);
  border: none;
  border-radius: .5rem;
  padding: 0 .5em;
}

input[type="color"] {
  width: 3.5rem;
}

textarea {
  background-color: var(--surface-color);
  resize: none;
  border: none;
  border-radius: .5rem;
  padding: .375em .5em;
}

@media (hover: hover) {
  button:hover {
    color: var(--accent-text-color);
    background-color: var(--accent-color);
  }

  label:hover + button {
    background-color: var(--surface-color);
  }

  button[data-theme]:hover {
    color: var(--accent-text-color);
    background-color: var(--accent-color);
  }
}

.flex-row {
  width: -moz-fit-content;
  width: fit-content;
  flex-flow: wrap;
  gap: .25rem;
  display: flex;
}

.flex-col {
  flex-direction: column;
  gap: .25rem;
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
  display: flex;
}

script ~ :not(script) {
  display: none !important;
}

ul, ol {
  counter-reset: list;
  margin: 0;
  padding: 0;
  list-style: none;
}

ol li {
  counter-increment: list;
}

ol li:before {
  content: counter(list) ". ";
}

span.code {
  pointer-events: none;
}

kbd {
  height: 1.5em;
  background-color: var(--surface-color);
  border-radius: .25rem;
  padding: 0 .25rem;
  font: 14px Poppins, sans-serif;
}

div.spacer {
  height: 1.5rem;
}

.attention {
  box-shadow: 0 0 0 .125rem var(--error-color);
  transition: box-shadow .1s ease-in;
}

.button-grid {
  grid-template-columns: repeat(auto-fit, minmax(2.5rem, 1fr));
  gap: .25rem;
  display: grid;
}

.theme-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: .25rem;
  display: grid;
}

button[data-theme] {
  height: 4rem;
  color: var(--text-color);
  background-color: var(--surface-color);
  text-transform: capitalize;
}

#clicker {
  width: min(100%, 800px);
  flex: 1;
  justify-content: center;
  align-self: center;
}

#answer-container {
  flex-direction: column-reverse;
  gap: .25rem;
  display: flex;
}

#answer-suggestion {
  height: 1.5rem;
}

#code-input {
  text-align: center;
  font-size: 2em;
}

#seat-grid {
  grid-template-columns: repeat(6, 1fr);
  gap: .25rem;
  display: grid;
}

#history-feed {
  max-height: 400px;
  border-radius: .5rem;
  overflow-y: auto;
}

#history-feed > button {
  height: -moz-fit-content;
  height: fit-content;
  text-align: left;
  padding: .375em .75em;
}

#history-feed > button > p {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 1rem;
}

::-webkit-scrollbar-track {
  background-color: #0000;
}

::-webkit-scrollbar-thumb {
  background-color: var(--surface-color);
  background-clip: content-box;
  border: .25rem solid #0000;
  border-radius: .5rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--accent-color);
}

